@import '../../../includes/assets/css/consts.scss';

/*Admin top bar*/
#wpadminbar {

	.dp-wpadmin-bar-tour-status {
		color: rgb(var(--dpu-color-4));
		font-variant: small-caps;
		font-size: 11px;
		font-weight: 600;
	}

	#wp-admin-bar-intro-actions {
		>li {
			>a {
				&::before {
					float: none;
					top: 5px;

				}
			}
		}

	}


	li.dpit-ab-list-item {
		&:not(.dpit-ab-list-item--main) {
			float: none !important;
		}

		>a.ab-item {
			height: 26px !important;
			display: flex !important;
			line-height: 2 !important;
			align-items: center;
			justify-content: space-between;

			>.dpit-ab-title {
				order: 1;
				display: inline-flex;
				align-items: center;
			}


			>.wp-admin-bar-arrow {
				display: block;
				order: 2;
				line-height: 1 !important;
				margin-left: 10px;

				&::before {
					position: static !important;
				}
			}
		}

		.dpit-ab-icon {
			line-height: 1 !important;

			svg {
				width: 16px;
				height: 16px;
			}

			&.dashicons {
				font-family: dashicons !important;
				font-size: 18px !important;
			}
		}

		.dpit-ab-text {
			line-height: 1.3 !important;
			max-width: 200px;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
			margin: 0 0 0 5px;

			@media (max-width: 782px) {
				max-width: 150px;
			}

			@media (max-width: 600px) {
				max-width: unset;
			}
		}

		.ab-sub-wrapper {
			align-self: flex-start;
		}


		&--main {
			>a.ab-item {
				height: 32px !important;
			}

			.dpit-ab-text {
				margin: 0 0 0 3px;
			}
		}
	}
}

body:not(.wp-admin) {
	li.dpit-ab-list-item {

		&:not(:hover):not(.hover) {
			.dpit-ab-icon {
				color: rgba(var(--dpu-color-2-text), 0.9) !important;
			}

			.dpit-ab-label {
				color: rgba(var(--dpu-color-2-text), 0.9) !important;
			}



		}

		&--global {
			&:not(:hover):not(.hover) {
				.dpit-ab-icon {
					color: rgb(var(--dpu-color-3)) !important;
				}
			}
		}

		// draft or deactivated tour
		&--not-active:not(:hover):not(.hover) {
			.dpit-ab-icon {
				color: rgba(var(--dpu-color-2-text), 0.6) !important;
			}

			.dpit-ab-label {
				color: rgba(var(--dpu-color-2-text), 0.6) !important;
			}
		}
	}
}